.navbar1 {
	background-color: #060b26;
	height: 80px;
	display: flex;
	justify-content: start;
	align-items: center;
}

.menu-bars1 {
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
}

.menu-relative {
	position: relative;
}

.nav-menu1 {
	background-color: #060b26;
	width: 250px;
	height: 93vh;
	justify-content: center;
	position: absolute;
	top: 0;
	left: -100%;
	transition: 850ms;
}

.nav-menu1.active {
	left: 0;
	transition: 350ms;
}

.nav-text1 {
	display: flex;
	justify-content: start;
	align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}

.nav-text1 a {
	text-decoration: none;
	color: #f5f5f5;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: flex;
	align-items: center;
	padding: 0 16px;
	border-radius: 4px;
}

.nav-text1 a:hover {
	background-color: #1a83ff;
}

.nav-menu-items1 {
	width: 100%;
}

.navbar1-toggle {
	background-color: #060b26;
	width: 100%;
	height: 80px;
	display: flex;
	justify-content: start;
	align-items: center;
}

span {
	margin-left: 16px;
}
