body, html {
    overflow-x: hidden;
  }
  
  .app-header {
    display:block;
    padding: 30px;
  }

  .button{
    color: red;
  }
  
  .app-title {
    display:inline-block;
    width: 100%;
    margin: 80px 0 0 0;
    float:left;
    position:relative;
    color:#fff;
    
    @media(min-width:768px) {
      width: 20%;
      margin: 280px 0 0 0;
    }
    
    h1 {
      font-size: 52px;
      margin: 0;
    }
    
    p {
      font-weight: 200;
      opacity: .5;
    }
    
  }
  
  .app-title-content {
    position:relative;
    bottom: 20%;
    left: 50%;
    width:100%;
    text-align:center;
    transform:translate(-50%, -50%);
    
    @media(min-width:768px) {
      width:auto;
      position:absolute;
      text-align:left;
    }
  }
  
  .app-card-list {
    display:inline-block;
    width:100%;
    margin:0;
    // min-height: 75vh;
    white-space:normal;
    overflow-x: scroll;
    // margin-top:70px;
    
    &::-webkit-scrollbar { 
      display: none; 
    }
    
    @media(min-width:768px) {
        width:80%;
        white-space:nowrap;
        margin-top: 0;
    }
    
    .card {
      margin: 0px 10px 70px;
      display: inline-block;
       width: 40%;
      .card-header {
        height: 100%;
      }
      .card-body {
          white-space: normal;
      }
    }
  }
  
  .designer-link {
    display:inline-block;
    padding-top:40px;
  }